$blue: #0B3142;
$white: #F0EDEE;
$red: #49111C;
$green: #0F5257;
$standard: 10px;
$card-height: 450px;
$card-width: 300px;

button{
  outline: none;
}
.background{
  margin-bottom: 30px;
}
.userCardContainer{
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}



.cardComponent{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.outer-div,
.inner-div {
      /* height: 450px; */
      max-width: 300px;
      /* margin: 4px auto; */
      margin-left: 9px;
      position: relative;
}

.outer-div {
  perspective: 900px;
  perspective-origin: 50% calc(50% - 18em);
}

.inner-div {
  margin: $standard * 5 auto;

  border-radius: 5px;
  font-weight: 400;
  color: $blue;
  font-size: 1rem;
  text-align: center;
  transition: all 0.6s cubic-bezier(0.8, -0.4, 0.2, 1.7);
  transform-style: preserve-3d;

  &:hover {
    transform: rotateY(180deg);
    background-color: transparent;
  }

  &:hover .social-icon {
    opacity: 1;
    top: 0;
  }

  &:hover .front__face-photo,
  &:hover .front__face-photo_Edited,
  &:hover .front__footer {
    opacity: 0;
  }
}

.front,
.backCardFace {
  position: relative;
  top: 0;
  left: 0;
  backface-visibility: hidden;
}

.front {
  cursor: pointer;
  height: 120%;
  background: $white;
  backface-visibility: hidden;
  border-radius: 5px;
  box-shadow: 0 15px 10px -10px rgba(0, 0, 0, 0.5), 0 1px 4px rgba(0, 0, 0, 0.3),
    0 0 40px rgba(0, 0, 0, 0.1) inset;
}

.front__bkg-photo {
  position: relative;
  height: 150px;
  width: $card-width;
  background-color: $blue;
  background-size: cover;
  backface-visibility: hidden;
  overflow: hidden;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;

  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
  }
}

.front__face-photo {
  position: relative;
    top: -60px;
    height: 120px;
    width: 125px;
    margin: 0 auto;
    border-radius: 50%;
    border: 0.5px solid #f6f6f6;
    background-color: #49111C;
    background-size: contain;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    overflow: hidden;
    transition: all 0.6s cubic-bezier(0.8, -0.4, 0.2, 1.7);
    z-index: 3;
    color: #F0EDEE;
