//  search panel

$ruler: 16px;
$color-red: #AE1100;
$color-bg: #EBECF0;
$color-shadow: #BABECC;
$color-white: #FFF;


.serchForm {
  padding: $ruler;
  width: $ruler*20;
  margin: 0 auto;
}

.segment {
  padding: $ruler*2 0;
  text-align: center;
}

.searchInput {
  border: 0;
  outline: 0;
  font-size: $ruler;
  border-radius: $ruler*20;
  padding: $ruler;
  background-color:$white;
  text-shadow: 1px 1px 0 $color-white;
}

.searchLabel {
  display: block;
  margin-bottom: $ruler*1.5;
  width: 100%;
}

.searchInput {
  margin-right: $ruler/2;
  box-shadow:  inset 2px 2px 5px $color-shadow, inset -5px -5px 10px $color-white;
  width: 100%;
  box-sizing: border-box;
  transition: all 0.2s ease-in-out;
  appearance: none;
  -webkit-appearance: none;

  &:focus {
    box-shadow:  inset 1px 1px 2px $color-shadow, inset -1px -1px 2px $color-white;
  }
}
.btnsCardDiv{
  display: flex;
  justify-content: space-between;
  margin: 5px;
  margin-right: 28px;
}
.cardDonateBtn{
  background-color: $red;
  border: 1px solid $red;
  color: #fff;
  text-decoration: none;
  // line-height: 22px;
  border-radius: 10px;
  font-size: 14px;
  font-weight: bold;
  position: relative;
  text-align: center;
}
.cardDonateBtn:hover{
  background-color: #000;
}
.filterSearchPanel{
  display: flex;
  justify-content: center;
  margin-top: 30px;
  input{
    width: fit-content;
    margin: 15px;
    outline: none;
    border-radius: 15px;
    box-shadow: 0 0 2px grey;
    color: $red;
}
input:focus{
  border: 1px solid $red;
}
}